.form input, .form textarea, .form select {
	border: none;
	background-color: transparent;
	font: normal 24px/1.618 'sinanova', Serif;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.form input:focus {
	outline: none;
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.form input[type="button"], .form input[type="submit"] {
    cursor: pointer;
}

/*
 * Search form
 */
.form.search .container {
	position: relative;
	margin: 5px auto;
	width: 100%;
	border-bottom: 1px solid #aaa;
}
.form.search .container input {
	float: left;
	padding: 8px;
}
.form.search .container input[type="button"], .form.search .container input[type="submit"] {
	display: block;
	width: 40px;
	margin: 0;
	padding: 12.5px;
	color: #aaa;
	font-size: 1em;
	background-color: transparent;
	border-radius: 0;
	box-shadow: none;
}
.form.search .container input[type="button"]:hover, .form.search .container input[type="submit"]:hover {
	color: #333;
}
.form.search .container input[name="q"] {
	width: 454px;
	text-align: center;
}

#results {
	margin: 30px 0 0;
	vertical-align: middle;
}
#results ol li {
    padding: 0 0 5px 15px;
}

/*
 * Edit forms
 */
.form.edit .row {
	position: relative;
	padding: 10px 3%;
	width: 94%;
}
.form.edit .row label {
	display: inline-block;
	width: 100%;
	text-align: left;
    color: #666;
}
.form.edit .row input[type="text"], .form.edit .row textarea, .form.edit .row select {
	width: 100%;
	font-size: 1.1em;
    color: #000;
	border-bottom: 1px solid #aaa;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.form.edit .row input[type="text"] {
	height: 28px;
}
.form.edit .row textarea {
	height: 200px;
}
.form.edit input[type="text"]:focus, .form.edit .row textarea:focus, .form.edit .row select:focus {
	outline: none;
	border-bottom-color: #333;
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.form.edit .row input.loading {
    color: #999;
    background-color: #ccc;
}
.form.edit input[type="button"], .form.edit input[type="submit"], a.button {
	position: relative;
	cursor: pointer;
	display: inline-block;
	margin: 4px;
	padding: 3px 12px;
    color: #333;
	background-color: #eee;
    border: 1px solid #999;
	border-radius: 2px;
	box-shadow: 2px 2px 2px #eee;
    font-size: 1em;
}
.form.edit input[type="button"]:hover, .form.edit input[type="submit"]:hover, a.button:hover,
.form.edit input[type="button"]:focus, .form.edit input[type="submit"]:focus, a.button:focus {
	background-color: #e6e6e6;
}
.form.edit input[type="button"]:active, .form.edit input[type="submit"]:active, a.button:active {
	top: 1px;
	left: 1px;
	box-shadow: 1px 1px 2px #eee;
}
a.button:after {
	visibility: hidden;
	transform: scaleX(0);
}
.form.edit .row input[disabled] {
	color: #999;
}

#keyboard {
    display: none;
    position: fixed;
    top: 5%;
    right: 10%;
    width: 200px;
    cursor: move;
	padding: 3px;
	text-align: center;
	background-color: #f7f7f7;
	border: 1px solid #aaa;
	border-radius: 2px;
	box-shadow: 4px 4px 10px #aaa;
	z-index: 10;
}

#keyboard .title {
    font-size: 0.5em;
    width: 100%;
    display: block;
    margin: 3px 0;
    text-align: center;
    letter-spacing: 2px;
}
#keyboard .close {
    position: absolute;
    right: -27px;
    top: -15px;
    cursor: pointer;
}
#keyboard .move {
    position: absolute;
    right: -30px;
    top: 15px;
}

@media only screen and (max-width: 600px) {

    .form.search .container {
        width: 100%;
        position: relative;
    }

    .form.search input {
        font-size: 0.8em;
    }

    .form.search .container input[name="q"] {
        width: 65%;
        margin: 0 2.5%;
        padding: 0;
    }

    .form.search .container input[type="button"], .form.search .container input[type="submit"] {
        width: 15%;
        margin: 0;
        padding: 0;
    }



}

/*
 * General styles.
 */
.dialog {
	position: fixed;
	top: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	display: none;
	color: #fff;
	background: transparent url('../../assets/img/shade.png') repeat;
}
.dialog > div {
	position: relative;
	display: block;
	margin: 20px auto;
	padding: 10px;
	width: 450px;
	font-size: 0.9em;
}
.dialog .close {
	position: relative;
	float: right;
	font-size: 1.2em;
}
.dialog h1 {
	margin: 0;
}
.dialog > div > div {
	margin: 5px auto;
}
.dialog a {
	color: #fff;
	font-weight: bold;
}
.dialog a:after {
	visibility: hidden;
	transform: scaleX(0);
}
.dialog a:hover {
	color: #85cdff;
}
.dialog.log > div > div {
	height: 400px;
	width: 100%;
	overflow: scroll;
}
.dialog .form select {
    margin: 0 4px;
    padding: 3px 5px 5px;
    color: #fff;
    font-size: 1.1em;
    text-align: center;
    letter-spacing: 2px;
    border-bottom: 1px solid #aaa;
    background-color: #333;
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
}
.dialog .form input {
    color: #fff;
    display: block;
}
.dialog .form input[type="text"] {
    width: 100%;
}
.dialog .form input.single {
    margin: 0 auto;
    width: 80% !important;
    text-align: center;
}
.dialog .form input[type="submit"], .dialog .form input[type="button"] {
    font-weight: bold;
    border-bottom: 2px solid #fff;
    margin: 0 20px;
}

/*
 * Semantic-UI search inputs.
 */
.dialog .semantic-search {
    position: relative;
    margin: 20px 0;
    height: 50px;
}
.dialog .semantic-search .results {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
}
.dialog .semantic-search .results .result,
.dialog .semantic-search .results .message {
    cursor: pointer;
    margin: 1px 0;
    padding: 6px 0;
    display: block;
    width: 100%;
    font-size: 1.2em;
    background-color: #333;
}
.dialog .semantic-search .results .content .title,
.dialog .semantic-search .results .message .header {
    position: relative;
    margin: 0;
    width: 100%;
    text-align: center;
    font-size: .9em;
}
.dialog .semantic-search .results .content .description,
.dialog .semantic-search .results .message .description {
    display: none;
}
.dialog .semantic-search.loading input[type="text"] {
   color: #777;
   background-color: #333;
}

/*
 * "Add resource" dialog.
 */
.dialog.resource input[type="text"], .dialog.resource input[type="submit"] {
    position: absolute;
    top: 0;
    height: 50px;
}
.dialog.resource input[type="text"] {
    left: 0;
    width: 90%;
    border-bottom: 1px solid #fff;
}
.dialog.resource input[type="submit"] {
    right: 0;
    width: 10%;
    margin: 0;
    border: none;
}
.dialog.resource .semantic-search .results {
   width: 90%;
}

/*
 * "Find a language" dialog.
 */
.dialog.language .semantic-search input[type="text"] {
    color: #333;
    font-size: 1em;
    line-height: 49px;
    background-color: #fff;
    border-radius: 25px;
}
.dialog.language .semantic-search .results .result,
.dialog.language .semantic-search .results .message {
    margin: 5px 0;
    background-color: transparent;
}
.dialog.language .semantic-search.loading input[type="text"] {
   color: #777;
   background-color: #333;
}

/*
 * Miscellaneous.
 */
.notices {
	height: auto;
	width: 100%;
	margin: 10px auto 20px;
	padding: 10px 0;
	text-align: center;
	border-top: 2px solid #999;
	border-bottom: 2px solid #999;
}
.notices .error {
    color: #990000;
}

.emphasis {
	height: auto;
	width: 100%;
	margin: 10px auto;
	padding: 10px 0;
	text-align: center;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 2px;
	box-shadow: 0 0 16px #eee inset;
}

